<template>
  <div>
      <div class="alert row" role="alert">
        <div class="col-lg-3 col-5 opp-tile">
          <img class='opp-tag' :src="oppType(opp)"/>
          <img class="opp-bg" src="https://cdn-expa.aiesec.org/gis-img/gv_default.png"/>
        </div>
        <div class="col-lg-6 col-6">
          <ul>
            <li>
              <a href="#">
                <h5 class="opp-title">{{ opp.title }}</h5>
              </a>
            </li>
            <li>
              <small>{{ opp.office['country'] }}</small>
              <span style="padding: 5px; color: #707070;">.</span>
              <small>{{ dateParser(opp.earliest_start_date) }}</small>
              <span style="padding: 5px; color: #707070;">.</span>
