          </div>
          <div class="applicants-nb col-lg-6">
            <small>{{ opp.applications_count }} applicants</small>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Opportunity',
  props: {
    opp : null
  },
  methods:{
    dateParser: function(_date){
      var date = new Date(_date);
      var day   = date.getDate();
      var month = date.getMonth();
      var year  = date.getFullYear();
      var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sept', 'Oct', 'Nov', 'Dec'];
      return (day + ' ' + months[month] + ' ' + year);
    },
    oppType: function(opp){
      var src;
      switch(opp.programmes.short_name){
        case "GV":
          src = "https://aiesec.org/assets/images/gv-logo-long.svg"; 
          break;
        case "GE":
          src = "https://aiesec.org/assets/images/ge-logo-long.svg";
          break;
        case "GT":
          src = "https://aiesec.org/assets/images/gt-logo-long.svg";
          break;
      }
      return src;
    },

  }
}
</script>